<template>
  <header>
    <div id="cLogo">
      <img id="logo" src="@/assets/icons/logo.webp" alt="img de LOGO" />
      <h2>Fruit Market</h2>

      <div id="menuIcon" @click="showMenu = !showMenu">
        <img src="@/assets/icons/menu.png" v-show="!showMenu" alt />
        <img src="@/assets/icons/menu-focus.png" v-show="showMenu" alt />
      </div>
    </div>

    <nav id="menu" v-show="showMenu">
      <ul>
        <li @click="clickMenu = !clickMenu">
          <router-link :to="{ name: 'About'}" :class="'button '+ routerActive[0]">Nosotros</router-link>
        </li>
        <li @click="clickMenu = !clickMenu">
          <router-link :to="{ name: 'Services'}" :class="'button '+ routerActive[1]">Servicios</router-link>
        </li>
        <li @click="clickMenu = !clickMenu">
          <router-link :to="{ name: 'Products'}" :class="'button '+ routerActive[2]">Productos</router-link>
        </li>
        <li @click="clickMenu = !clickMenu">
          <router-link :to="{ name: 'Support'}" :class="'button '+ routerActive[3]">Soporte</router-link>
        </li>
        <li @click="clickMenu = !clickMenu">
          <router-link :to="{ name: 'Contact'}" :class="'button '+ routerActive[4]">Contacto</router-link>
        </li>
        <li>
          <button title="Opciones" @click="clickMenu = !clickMenu"></button>
        </li>
      </ul>
    </nav>
  </header>
</template>

<script>
export default {
  props: { windowWidth: Number },
  data() {
    return {
      showMenu: false,
      clickMenu: false,
      routerActive: ""
    };
  },
  mounted() {
    this.showMenuFun();
    this.buttonActive();
  },
  methods: {
    buttonActive() {
      this.routerActive = ["", "", "", "", ""];

      switch (this.$route.name) {
        case "About":
          this.routerActive[0] = "isActive";
          break;
        case "Services":
          this.routerActive[1] = "isActive";
          break;
        case "Products":
          this.routerActive[2] = "isActive";
          break;
        case "ProductsCategory":
          this.routerActive[2] = "isActive";
          break;
        case "ProductDetail":
          this.routerActive[2] = "isActive";
          break;
        case "Support":
          this.routerActive[3] = "isActive";
          break;
        case "Contact":
          this.routerActive[4] = "isActive";
          break;
      }
    },
    showMenuFun() {
      if (this.windowWidth > 950) {
        this.showMenu = true;
      } else {
        if (this.clickMenu == true) {
          this.clickMenu = false;
        }
        this.showMenu = false;
      }
    }
  },
  watch: {
    $route: "buttonActive",
    windowWidth() {
      this.showMenuFun();
    },
    clickMenu: "showMenuFun"
  }
};
</script>

